
import Spinner from 'vux/src/components/spinner/index.vue'

import api from '../api/api'
import env from '../config/env'

export default {
    props: [
        'category'
    ],
    data() {
        return {
            productList: [],
            showLoading: false,
            loadingType: 'lines',
            iconStyle: 'icon-remaituijian'
        }
    },
    mounted() {
        this.initData()
    },
    watch: {
        category(newVal) {
            // /course  =>  /goods 调用
            // 这里有个bug，当主菜单切换的时候，也会触发改变，此时category为空，所以在这里多加了个if判断
            if(newVal) {
                this.initData()
            }
        }
    },
    methods: {
        async initData() {
            let vm = this
            // 显示加载动画
            vm.showLoading = true
            // 服务端获取产品列表，一次获取10条记录
            api.get('getProductList', 
                    {category: vm.category, startLimit: 0, pageSize: 99}
                ).then((res) => {
                if(res.status===1){
                    // 转换数组
                    vm.productList = res.data
                    if(vm.category==='course'){
                        this.iconStyle = "icon-remaituijian"
                    }else{
                        this.iconStyle = "icon-zhekoutuijian"
                    }
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '确定',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
                // 隐藏加载动画
                vm.showLoading = false
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        getImgUrl(thumbnail_img_url) {
            return env.serverUrl + thumbnail_img_url
        }
    },
    components: {
        Spinner
    }
}



